import {Injectable} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {Scenario} from "../@creation/beans/Scenario";
import {Observable} from "rxjs";
import {ScenarioList} from "./beans/ScenarioList";
import { map } from 'rxjs/operators';


@Injectable()
export class HomeService {
    constructor(
        private _http: HttpClient) {
    }

    public getScenarioList(): Observable<any> {

        return this._http.get("/scenario")
               .pipe(
                    map( (res) => {
                      let data = res;
                      return data
                    }));

        //return this._http.get<Scenario[]>("/scenario").scenarioResponseList;
    }

}